/* Transitions: Fade / Collapse */
.fade {
	@apply transition-opacity ease-linear;
}

.collapse:not(.show) {
	@apply hidden;
}

.collapsing {
	@apply h-0 overflow-hidden transition-height;
}

/* Likes */
.js-likes--active svg {
	@apply opacity-0;
}

